<template>
    <div>
        <h3>{{ id?'编辑文章':'新增文章' }}</h3>
        <a-card>
            <a-form-model :model="artInfo" ref="artInfoRef" :rules="artInfoRules" :hideRequiredMark="true">
                <a-form-model-item label="文章标题" prop="title">
                    <a-input style="width:300px" v-model="artInfo.title"></a-input>
                </a-form-model-item>
                <a-form-model-item label="文章描述" prop="desc">
                      <a-input type="textarea" v-model="artInfo.desc"></a-input>
                </a-form-model-item>
                <a-form-model-item label="文章分类" prop="cid">
                    <a-select style="width:200px" v-model="artInfo.cid" placeholder="请选择分类">
                        <a-select-option v-for="item in Catelist" :key="item.id" :value="item.id">
                            {{ item.name }}
                        </a-select-option>
                    </a-select>
                </a-form-model-item>
                <a-form-model-item label="文章缩略图" prop="img">
                    <a-upload
                    list-type="picture"
                    name="file"
                    :multiple="false"
                    :action="upUrl"
                    :headers="headers"
                    @change="upChange"
                    >
                    <a-button> <a-icon type="upload" /> 点击上传 </a-button>
                    <br/>
                    <template v-if="id">
                      <img :src="artInfo.img" style="width:150px;height:80px">
                    </template>
                    </a-upload>
                </a-form-model-item>
                <a-form-model-item label="文章内容" prop="content">
                </a-form-model-item>
                <a-form-model-item>
                    <a-button type="primary" style="margin-right:15px" @click="artSubmit(artInfo.id)"> {{ artInfo.id?'更新':'提交' }} </a-button>
                    <a-button type="danger" @click="addCancel">取消</a-button>
                </a-form-model-item>
            </a-form-model>
        </a-card>
    </div>
</template>

<script>
import { Url } from '../../plugins/http'
export default {
  props: ['id'],
  data () {
    return {
      artInfo: {
        id: 0,
        title: '',
        cid: undefined,
        desc: '',
        content: '',
        img: ''
      },
      artInfoRules: {
        title: [{ required: true, message: '请输入文章标题', trigger: 'blur' }],
        cid: [{ required: true, message: '请输入文章分类', trigger: 'change' }],
        desc: [
          { required: true, message: '请输入文章描述', trigger: 'blur' },
          { max: 120, message: '描述最多不超过120字符', trigger: 'change' }
        ],
        img: [{ required: true, message: '请选择文章缩略图', trigger: 'blur' }],
        content: [{ required: true, message: '请输入文章内容', trigger: 'blur' }]

      },
      Catelist: [],
      upUrl: Url + 'upload',
      headers: {}
    }
  },
  created () {
    this.getCateList()
    this.headers = { Authorization: `Bearer ${window.sessionStorage.getItem('token')}` }
    if (this.id) {
      this.getArtInfo(this.id)
    }
  },
  methods: {
    // 查询文章信息
    async getArtInfo (id) {
      const { data: res } = await this.$http.get(`article/${id}`)
      if (res.status !== 200) return this.$message.error(res.message)
      this.artInfo = res.data
      this.artInfo.id = res.data.ID
    },
    // 获取分类列表
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.status !== 200) return this.$message.error(res.error)
      this.Catelist = res.data
    },
    // 切换选择分类时触发
    /* cateChange (value) {
      this.artInfo.cid = value
    } */
    // 上传图片
    upChange (info) {
      if (info.file.status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (info.file.status === 'done') {
        this.$message.success('图片上传成功')
        this.artInfo.img = info.file.response.url
      } else if (info.file.status === 'error') {
        this.$message.error('图片上传失败')
      }
    },
    // 提交或者更新文章
    artSubmit (id) {
      this.$refs.artInfoRef.validate(async (valid) => {
        if (id === 0) {
          const { data: res } = await this.$http.post('article/add', this.artInfo)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('添加文章成功')
          this.$router.push('/artlist')
        } else {
          const { data: res } = await this.$http.put(`article/${id}`, this.artInfo)
          if (res.status !== 200) return this.$message.error(res.message)
          this.$message.success('更新文章成功')
          this.$router.push('/artlist')
        }
      })
    },
    addCancel () {
      this.$refs.artInfoRef.resetFields()
    }
  }
}
</script>
